/**
 * Classe responsvel por fornecer servios relacionados  autenticao e controle de login.
 */
class LoginService {
  /**
   * Lista de cargos de usurio vlidos.
   * @type {string[]}
   */
  public static userRoles = ["loggedIn", "loggedOut"];

  /**
   * Usurio logado atualmente.
   * @type {string}
   * @private
   */
  private static loggedUser = "loggedIn";

  /**
   * Altera o usurio logado para o cargo de usurio especificado.
   * @static
   * @param {string} role - O cargo de usurio para o qual fazer login.
   * @returns {string} - O cargo de usurio atual aps a alterao.
   */
  static loginAs = (role: string): string => {
    return (this.loggedUser = role);
  };

  /**
   * Obtm o cargo do usurio atualmente logado.
   * @static
   * @returns {string} - O cargo de usurio atual.
   */
  static getLogin = (): string => this.loggedUser;
}

export default LoginService;
